ader .slider__controls .slick-next{margin:9px 5px 0 5px}}@media screen and (min-width: 814px){.slider-header .slider__controls .position{margin:0 20px 0 10%}}@media screen and (min-width: 1024px){.slider-header .slider__controls .position{margin:0 20px 0 15%}}.slide-item{position:relative}.slide-item .slide-item__caption{display:none}.slide-item__content{position:absolute;top:0;left:0;right:0;bottom:0;background:#00833E;opacity:0.9;color:#FFFFFF;font-weight:600;display:flex;flex-flow:column nowrap;padding:15px 10px}.slide-item__content p{line-height:20px;font-size:16px}.slide-item__content .slider-item__cta{margin-top:auto;color:#FFFFFF;border:3px solid #FFFFFF;font-size:13px;padding:5px 10px;width:195px}.slide-item__content .slider-item__cta i{margin-left:15px}.slide-item__content .slider-item__cta:hover{color:#FFFFFF;text-decoration:none}.slide-item__content .slider-item__cta:visited{color:#FFFFFF}@media screen and (min-width: 380px){.slide-item__content{padding:20px 40px}}@media screen and (min-width: 480px){.slide-item__content{top:10%;bottom:10%;rigth:0;left:unset;width:400px;padding:25px 40px}.slide-item__content p{font-size:18px}.slide-item__content .slider-item__cta{font-size:16px;width:225px}}@media screen and (min-width: 568px){.slide-item__content{top:15%;bottom:15%;width:450px}.slide-item__content p{line-height:25px}}@media screen and (min-width: 834px){.slide-item__content{top:35%;width:500px}.slide-item__content p{font-size:20px;line-height:30px}}@media screen and (min-width: 1024px){.slide-item__content{top:unset;bottom:20%;width:650px}.slide-item__content p{font-size:24px;line-height:30px;margin-bottom:40px}}

